import React from 'react'
import { Row, Col, Form } from 'antd'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import useRouteLeaveConfirm from '@/hooks/useRouteLeaveConfirm'
import { useAllReportTemplates } from '@/services/reportTemplate'
import Select from '@/components/select'
import Input from '@/components/input'
import { Page } from '@/components/layout'
import Button from '@/components/button'
import { rules } from '@/utils/form'
import history from '@/utils/history'
import { REPORT_INPUT } from '@/constants/routes'
import { toQueryString } from '@/utils/request'

export default function ReportInquiry() {
  useTitle('輸入報告')

  useBreadcrumb([{ name: '輸入報告' }])

  const handleConfirm = useRouteLeaveConfirm('/')

  useHeaderContent(
    <Button
      color="outline"
      className="text-white border-white bg-transparent"
      onClick={handleConfirm}>
      取消
    </Button>
  )

  const [form] = Form.useForm()

  const [reportTemplates] = useAllReportTemplates()

  const handleOk = () => {
    form.validateFields().then(values => {
      history.push(`${REPORT_INPUT}?${toQueryString(values)}`)
    })
  }

  return (
    <>
      <Page style={{ height: 'calc(100vh - 203px)' }}>
        <Row justify="center">
          <Col span={20}>
            <div className="bg-white p-12 mb-8 rounded-lg shadow-lg">
              <Form
                form={form}
                layout="inline"
                className="flex items-center"
                hideRequiredMark={true}
                colon={false}>
                <Form.Item
                  className="flex items-center"
                  label="審核醫師"
                  rules={[rules.required]}
                  name="reviewDoctor">
                  <Select
                    allowClear
                    size="large"
                    placeholder="選擇醫師"
                    style={{ width: 160 }}>
                    <Select.Option value={'李懷寶'}>李懷寶</Select.Option>
                    <Select.Option value={'林榮嘉'}>林榮嘉</Select.Option>
                  </Select>
                </Form.Item>
                <Form.Item
                  className="flex items-center"
                  label="申請序號"
                  rules={[rules.required]}
                  name="serialNo">
                  <Input
                    size="large"
                    placeholder="輸入申請序號"
                    style={{ width: 230 }}
                  />
                </Form.Item>
                <Form.Item
                  rules={[rules.required]}
                  className="flex items-center"
                  name="templateId"
                  label="報告格式">
                  <Select size="large" style={{ width: 230 }}>
                    {reportTemplates.map(x => (
                      <Select.Option key={x.id} value={x.id}>
                        {x.name}
                      </Select.Option>
                    ))}
                  </Select>
                </Form.Item>
                <div className="flex justify-end">
                  <Button
                    size="large"
                    color="primary"
                    style={{ width: 120 }}
                    onClick={handleOk}>
                    確認
                  </Button>
                </div>
              </Form>
            </div>
          </Col>
        </Row>
      </Page>
      {/* )} */}
    </>
  )
}
